@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

html {
  overflow: hidden;
}

body {
  font-family: Raleway, Verdana, Geneva, sans-serif;
  line-height: 1.4;
  color: white;
}

button {
  all: unset;
  color: white;
  font-size: 4rem;
}
a {
  text-decoration: none;
  color: white;
}


i:hover {
  opacity: .5;
}

h1 {
  font-size: 3rem;
  text-transform: uppercase;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

h2 {
  font-size: 2rem;
  text-transform: uppercase;
}

h3 {
  font-size: 1.5rem;
  text-transform: uppercase;
}

hr {
  margin: 30px;
  margin-left: 0px;
  margin-right: 0px;
  -webkit-animation-name: lineOut;
          animation-name: lineOut;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

p {
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.navlist {
  width: 100%;
  top: 0px;
  list-style: none;
  margin-right: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  bottom: 0px;
}

.navlist li {
  display: flex;
  flex-direction: column-reverse;
}

.link {
  text-decoration: none;
  color: white;
}

.link:hover {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  overflow: hidden;
  background-image: url(/interactive-resume/static/media/bg.c3429fcc.jpeg);
  background-size: cover;
}

.layer {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-animation: fadeIn;
          animation: fadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.active {
  font-size: 3rem;
}

.heading {
  -webkit-animation-name: slideIn;
          animation-name: slideIn;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.overflow {
  overflow: hidden;
  height: 400px;
  margin-bottom: 50px;
  padding-bottom: 50px;
}

.component {
  height: 100vh;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.container {
  width: 1000px;
  height: 100%;
  display: flex;
  align-content: center;
  flex-direction: column;
  text-align: left;
  overflow: scroll;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.title {
  display: none;
}

.large-screen {
  display: flex;
  padding: 50px;
  padding-bottom: 0;
}

.large-screen h1 {
  margin-bottom: 0;
  line-height: 1;
}

.drop-menu {
  display: none;
}

.aboutme-boxes {
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.aboutme-boxes img {
  border-radius: 50%;
  border: 0.2rem solid white;
  margin: 20px;
}

.aboutme-box p {
  text-align: left;
  padding: 20px;
  padding-left: 0px;
}

.experiences-box {
  display: flex;
  padding: 0;
  margin: 0;
}

.experiences-title {
  text-align: left;
  padding: 0;
  margin: 0;
  width: 30%;
}

.experiences-description {
  text-align: left;
  padding: 0;
  margin: 0;
  max-width: 70%;
}

.fadeIn {
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  padding: 50px;
  padding-top: 0px;
  overflow: scroll;
}

.dropped {
  display: none;
}

.contact-boxes {
  display: flex;
  font-size: 4rem;
  color: white;
  flex-direction: row;
  justify-content: space-evenly;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes fadeOut {
  from {
    opacity: 0;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
  }
}

@keyframes fadeOut {
  from {
    opacity: 0;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
  }
}

@-webkit-keyframes lineOut {
  from {
    left: 50%;
    width: 0%;
  }
  to {
    left: 5%;
    width: 90%;
  }
}

@keyframes lineOut {
  from {
    left: 50%;
    width: 0%;
  }
  to {
    left: 5%;
    width: 90%;
  }
}
@-webkit-keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@media screen and (max-width: 1025px) {
  hr {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .container {
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    flex-direction: column;
    text-align: left;
    overflow-x: hidden;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.4);
    padding: 0;
    margin: 0;
  }

  .component {
    padding: 0;
    margin: 0;
  }

  .aboutme-boxes {
    grid-template-columns: 1fr;
    padding: 0;
    margin: 0;
  }

  .aboutme-box2 {
    justify-self: center;
  }

  .experiences-box {
    flex-direction: column;
  }

  .experiences-title {
    width: 100%;
    padding-bottom: 50px;
  }

  .experiences-description {
    max-width: 100%;
  }

  .large-screen {
    display: none;
  }

  .navlist {
    flex-direction: column;
    justify-content: flex-start;
  }

  ul li {
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }

  .fadeIn {
    margin-top: 0px;
    padding: 20px;
    padding-top: 0px;
    padding-bottom: 0px;
    overflow: scroll;
  }

  .drop-menu {
    display: flex;
    height: 50px;
    width: 100%;
  }

  .drop-menu h1 {
    padding: 20px;
    padding-bottom: 0px;
    font-size: 2rem;
  }

  .fa-angle-down {
    font-size: 2rem;
    color: white;
    position: absolute;
    right: 30px;
    top: 30px;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  }

  .fa-angle-up {
    font-size: 2rem;
    color: white;
    position: absolute;
    right: 30px;
    top: 30px;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  }

  .title {
    display: block;
    padding: 20px;
    padding-bottom: 0px;
  }

  .site-head-sm {
    margin-top: 50px;
    margin-right: 30px;
    font-size: 1.8rem;
  }

  .dropped {
    margin-top: 30px;
    font-size: 1rem;
    margin-right: 30px;
    display: inline-block;
  }
}

